@font-face {
  font-family: 'Abril Fatface';

  src: url('../fonts/AbrilFatface-Regular.ttf') format('truetype');
  font-weight: 400;
}

@font-face {
  font-family: 'PPAgrandir';

  src: url('../fonts/PPAgrandir-GrandLight.otf') format('opentype');
  font-weight: 200;
}

@font-face {
  font-family: 'PPAgrandir';

  src: url('../fonts/PPAgrandir-WideLight.otf') format('opentype');
  font-weight: 300;
}

:root {
  --primary-color: #1d9552;
  --background-color: #ededed;
  --black-color: #141414;
}

[data-theme='blue'] {
  --primary-color: #3AB0FF;
  --background-color: #F9F2ED;
  --black-color: #141414;
}

*,*::before, *::after {
   padding: 0;
   margin: 0;
   box-sizing: border-box;
}

body {
  color: var(--primary-color);
  font-family: "PPAgrandir", Helvetica, Arial, sans-serif;
  padding: 0;
  margin: 0;
  font-size: 1vw;
}

h1 {
  font-size: 62px;
  font-family: 'Abril Fatface', Georgia, 'Times New Roman', Times, serif;
  letter-spacing: 3px;
  line-height: 1.6;
  border-bottom: 2px solid var(--primary-color);
  margin: 0;
  padding: 10px;
}

.Divide {
  width: 100%;
  height: 2px;
  background-color: var(--primary-color);
}

h2 {
  font-size: 42px;
  font-family: 'Abril Fatface', Georgia, 'Times New Roman', Times, serif;
  letter-spacing: 3px;
  margin: 0;
}

h5 {
  font-size: 18px;
  font-family: 'PPAgrandir', Georgia, 'Times New Roman', Times, serif;
  margin: 0;
}

.Resume {
  display: flex;
  position: relative;
  color: var(--primary-color);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
  width: 780px;
  padding: 40px;
  background-color: var(--background-color);
}

.About {
  position: relative;
}

.lightType {
  font-family: 'PPAgrandir', Georgia, 'Times New Roman', Times, serif;
  font-weight: 300;
}

.About-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 150px;
  align-items: center;
  padding-top: 30px;
  padding-bottom: 30px;
  margin: 0;
  border-bottom: 2px solid var(--primary-color);
}

main {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 24px;

}

.Education-wrapper {
  width: 50%;
  border-right: solid 2px var(--primary-color);
}

.Project-wrapper {
  padding-left: 20px;
  width: 50%;
  
}

.About {
  width: 100%;
}

.editForm {
  max-width: 200px;
  position: absolute;
  right: 0;
  display: none;
  flex-direction: column;
  padding: 10px;
  background-color: var(--background-color);
  border: 1px solid var(--primary-color);
  z-index: 99;

}
.editForm2 {
  max-width: 200px;
  position: absolute;
  right: 0;
  display: flex;
  flex-direction: column;
  padding: 10px;
  background-color: var(--background-color);
  border: 1px solid var(--primary-color);
  z-index: 99;
}

label {
  color: #141414;
}

input {
  appearance: none;
  background: none;
  font-family: 'PPAgrandir', Georgia, 'Times New Roman', Times, serif;
  border: 1px solid var(--primary-color);
  padding: 4px;
  margin-bottom: 12px;
  margin-top: 4px;
  color: var(--black-color);
}

input:focus {
  border: 1px solid var(--primary-color);
  outline: none;
}

input::placeholder {
  font-family: 'PPAgrandir', Georgia, 'Times New Roman', Times, serif;
}

textarea {
  appearance: none;
  background: none;
  font-family: 'PPAgrandir', Georgia, 'Times New Roman', Times, serif;
  border: 1px solid var(--primary-color);
  padding: 3px;
 
}

textarea:focus {
  outline: none;
}
textarea::placeholder {
  font-family: 'PPAgrandir', Georgia, 'Times New Roman', Times, serif;

}

.button-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;

}

button {
  font-family: 'PPAgrandir', Georgia, 'Times New Roman', Times, serif;
  padding: 6px 8px 6px 8px;
  background: none;
  border: 1px solid var(--primary-color);
  margin-top: 8px;
  margin-right: 20px;
  color:  var(--black-color);
  cursor: pointer;
}

.Edit {
  display: none;
  position: absolute;
  right: 0;
  margin-right: 5;
  color: var(--primary-color);
  cursor: pointer;
}

.Hover-area {
 width: 100%;
 height: 100%;
 position: absolute;
 z-index: 1;
}

.Qualification, .Work, .Project, .Contact {
  position: relative;
  padding-right: 10px;
}

.projectWrapper, .educationWrapper, .workWrapper, .contactWrapper {
  position: relative;
}

.elementWrapper {
  margin-top: 10px;
  margin-bottom: 10px;
}

.btn-wrapper {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;

}